import React from 'react';
import '../css/ArmorItem.css'

/**
 * Required props:
 *      - onClick(modalType, item.itemID)
 *      - item
 *      - modalType
 */
function ArmorItem(props) {
    return (
        <div key={props.item.itemID}
             className="itemDisplay"
             onClick={props.onClick}>
            <div className="itemImageContainer">
                <img className="itemImage" src={`${process.env.PUBLIC_URL}/images/${props.item.itemID}.png`}
                     alt={props.item.name}/>
            </div>
            <span className="itemCaption">{props.item.name}</span>
        </div>
    );
}

export default ArmorItem;